import React, { useState, useMemo, Suspense, lazy } from 'react';
import { ThemeProvider, CssBaseline, Box } from '@mui/material';
import { HashRouter as Router, Routes, Route, useLocation, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './auth/AuthContext';
import { MessageProvider } from './context/MessageContext';
import createCustomTheme from './theme/theme';
import NavbarP from './components/NavbarP';

// تحميل المكونات بشكل ديناميكي
const Faq = lazy(() => import('./components/faq'));
const Rest = lazy(() => import('./components/Rest'));
const LoginP = lazy(() => import('./components/LoginP'));
const Cart = lazy(() => import('./components/Cart'));
const Home = lazy(() => import('./components/Home'));
const Profile = lazy(() => import('./components/Profile'));
const Cvmaker = lazy(() => import('./components/Cvmaker'));
const Law = lazy(() => import('./components/Law'));
const Works = lazy(() => import('./components/Works'));
const InformisonWeb = lazy(() => import('./components/InformisonWeb'));
const Callus = lazy(() => import('./components/Callus'));
const Manger = lazy(() => import('./components/Manger'));
const Registers = lazy(() => import('./components/Registers'));

// مكون لحماية الصفحات التي تتطلب تسجيل دخول
function ProtectedRoute({ children }) {
  const { currentUser } = useAuth();
  const location = useLocation();

  if (!currentUser) {
    return <Navigate to="/LoginP" state={{ from: location }} replace />;
  }

  return children;
}

function AppContent() {
  const [mode, setMode] = useState(() => {
    const savedMode = localStorage.getItem('themeMode');
    return savedMode || 'dark';
  });

  const theme = useMemo(() => createCustomTheme(mode), [mode]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box
        sx={{
          minHeight: '100vh',
          backgroundColor: theme.palette.background.default,
          position: 'relative',
        }}
      >
        <NavbarP />
        <Box component="main">
          <Suspense fallback={<h1>جاري التحميل...</h1>}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/LoginP" element={<LoginP />} />
              <Route path="/register" element={<Registers />} />
              <Route path="/cvmaker" element={<ProtectedRoute><Cvmaker /></ProtectedRoute>} />
              <Route path="/profile" element={<ProtectedRoute><Profile /></ProtectedRoute>} />
              <Route path="/cart" element={<ProtectedRoute><Cart /></ProtectedRoute>} />
              <Route path="/rest" element={<ProtectedRoute><Rest /></ProtectedRoute>} />
              <Route path="/law" element={<Law />} />
              <Route path="/InformisonWeb" element={<InformisonWeb />} />
              <Route path="/Callus" element={<Callus />} />
              <Route path="/faq" element={<Faq />} />
              <Route path="/works" element={<Works />} />
              <Route path="/manger" element={<Manger />} />
              <Route path="*" element={<Navigate to="/" />} /> {/* إعادة توجيه أي مسار غير معروف إلى الصفحة الرئيسية */}
            </Routes>
          </Suspense>
        </Box>
      </Box>
    </ThemeProvider>
  );
}

function App() {
  return (
    <AuthProvider>
      <MessageProvider>
        <Router> {/* استخدم HashRouter لحل مشاكل المسارات */}
          <AppContent />
        </Router>
      </MessageProvider>
    </AuthProvider>
  );
}

export default App;
